.collection-item{width:22%;display:flex;flex-direction:column;height:350px;align-items:center;position:relative}.collection-item .image{width:100%;height:95%;background-size:cover;background-position:center;margin-bottom:5px}.collection-item .image .custom-button{width:80%;opacity:0.7;position:absolute;top:255px;display:none}.collection-item .image:hover .image{opacity:0.8}.collection-item .image:hover .custom-button{opacity:0.85;display:flex}.collection-item .image .collection-footer{width:100%;height:5%;display:flex;justify-content:space-between;font-size:18px}.collection-item .image .collection-footer .name{width:90%;margin-bottom:15px}.collection-item .image .collection-footer .price{width:10%}
